.lib-grid {
  display: grid;
  grid-template-columns: 230px 1fr;
  grid-template-rows: auto 1fr auto;
}

@media (max-width: 768px) {
  .lib-grid {
    grid-template-columns: 1fr;
  }
}

.lib-grid .lib-footer {
  grid-column: 1/-1;
}

.lib-grid .lib-side-nav {
  grid-column: 1/2;
}

@media (max-width: 768px) {
  .lib-grid .lib-side-nav {
    grid-column: 1/-1;
  }
}

.lib-grid .lib-body {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  width: 100%;
  max-width: 93.75rem;
}

@media (max-width: 768px) {
  .lib-grid .lib-body {
    grid-column: 1/-1;
  }
}

.lib-grid .lib-body main {
  grid-column: 1/9;
}

.lib-grid .lib-body .layout-sidebar-first {
  grid-column: 9/12;
}

.lib-full {
  grid-column: 1/-1;
}

body::before {
